<template>
  <div>
    <v-list class="ml-5" v-for="item in articleList" :key="item.id">
      <v-list-item>
        <v-list-item-content>
          <v-list-item-title style="cursor: pointer;">
            <b class="dots"></b>
            <span
              id="art_name"
              @click="handleToDetail(item.id)"
              class="black--text"
              >{{ item.title }}</span
            >
          </v-list-item-title>
        </v-list-item-content>
      </v-list-item>
      <v-divider light></v-divider>
    </v-list>
    <div class="text-center">
      <v-pagination
        v-model="page"
        class="my-4"
        :length="pages"
        :total-visible="7"
        @input="getSubjectList()"
        @next="getSubjectList()"
        @previous="getSubjectList()"
      ></v-pagination>
    </div>
  </div>
</template>

<script>
export default {
  name: 'List',
  props: ['subject'],
  watch: {
    subject() {
      this.innerSubject = this.subject
    },
    $route() {
      this.getSubjectList()
    },
  },
  data() {
    return {
      page: 1,
      pages: 1,
      innerSubject: this.subject,
      articleList: [],
    }
  },
  methods: {
    handleToDetail(id) {
      this.$router.push({
        path: this.innerSubject + '/detail/' + id,
        query: {
          enterprise: this.$route.query.enterprise,
          year: this.$route.query.year,
        },
      })
    },
    isUndef(obj) {
      if (obj === undefined) {
        return true
      } else return false
    },
    async getSubjectList() {
      var enterprise = this.$route.query.enterprise
      if (this.isUndef(this.$route.query.enterprise)) {
        enterprise = ''
      }
      var year = this.$route.query.year
      if (this.isUndef(this.$route.query.year)) {
        year = ''
      }
      if (!this.innerSubject) {
        this.innerSubject = '机械'
      }
      let res = await this.$http.get(
        '/api/school/select?need=' +
          this.innerSubject +
          '&page=' +
          this.page +
          '&enterprise=' +
          enterprise +
          '&year=' +
          year
      )
      if (res.status != 200) return false
      if (res.data.code != 200) {
        this.articleList = res.data.data
        return false
      }
      this.articleList = res.data.data.enterpriseneeds.list
      this.pages = res.data.data.enterpriseneeds.pages
    },
  },
  mounted() {
    this.getSubjectList()
  },
}
</script>

<style scoped>
#art_name:hover {
  margin-left: 30px;
}
.dots {
  width: 4px;
  height: 4px;
  background-color: #003366;
  display: inline-block;
  margin-right: 20px;
}
</style>
